@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');body {
  margin: 0;
  font-family: 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
h3{
  text-align: left;
  font-weight: 600;
  font-size:1.3em;
  letter-spacing: 1.5px;
  display: flex;
  align-items: center;
}
h3::before{
  content: '';
  display: inline-block;
  width: 5px;
  height: 40px;
  margin-right:8px;
  background-color: orange;
}
.page{
  text-align: left;
  display: flex;
  justify-content: center;
}
.page-content{
  padding: 10px 30px;
  width: 100%;
}
p, span{
  color: rgb(88, 88, 88);
  font-weight: 400;
  font-size: 0.95em;
  line-height: 35px;
}
.overview p{
  width: 40%;
  padding-right: 100px;
}
img{
  width: 300px;
  height: 300px;
  box-shadow: 0 0 5px grey;
}
.img-container{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  row-gap: 20px;
  margin-top: 50px;
}
.img-container img{
  height: 250px;
  width: 250px;
  margin:0 10px;
  box-shadow: none;
}
.equations-container span{
  font-size: 1em;
}